import { _decorator, Component, instantiate, Prefab } from "cc";
const { ccclass, property } = _decorator;

@ccclass('GameManager')
export class GameManager extends Component {

    @property({
        type: Prefab,
    })
    BlockPrefab: Prefab;

    public createBlock() {
        for (let i = 0; i < 64; i++) {
            let node = instantiate(this.BlockPrefab);
        }
    }
}


